import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Figure from 'react-bootstrap/Figure';

function languageFigure(langName) {
  var spaceRemoved = langName.replace(/\./g,' ')
  const path = "/images/skillsets/"+spaceRemoved.toLowerCase().replace(/\s/g,'')+".png";

  const imageStyle = {
    display: 'block',
    margin: '0 auto'
  };
  const figureStyle = {
    width: "100%",
    margin: '0 auto',
    display: 'inline-block'
  };
  const captionStyle = {
    textAlign: "center",
    fontSize: '2vw',
    color:'white'
  };
  const colStyle = {
    margin: '0 0px'
  };
  const yearStyle= {
    color: 'white',
    textAlign: "center",
    fontSize: '3vw'
  };
  const rowStyle = {
    margin: '25px 0px'
  };
  const yearPos = {
    margin: 'auto 0'
  }
  return (
    <Row style={rowStyle} className="justify-content-center">
      <Col style={colStyle} xs={6} sm={6} md={6} lg={6}>
        <Figure style={figureStyle}>
          <Figure.Image
          alt={langName}
          src={path}
          style={imageStyle}
          />
          <Figure.Caption style={captionStyle}>
            {langName}
          </Figure.Caption>
        </Figure>
      </Col>
      <Col style={yearPos} xs={4} sm={4} md={4} lg={4}>
        <h1 style={yearStyle}>1 Year</h1>
      </Col>
    </Row>);
}

function stackColumn(stack) {
  var stackName = stack['name']
  var languages = stack['languages']
  const colStyle = {
    padding:'0'
  };
  const headerStyle = {
    color: 'white',
    fontSize: '5vw',
    textAlign: 'center',
    margin: '0 auto'
  };
  const headerRowStyle = {
    position: 'relative',
    margin: '0 auto'
  }
  return (
    <Col style={colStyle} xs={6} sm={6} md={4} lg={4}>
      <Row>
        <h1 style={headerStyle}>{stackName}</h1>
      </Row>
      {languages.map(lang => languageFigure(lang))}
    </Col>
  );
}

function Skillsets() {
  var stacks = [
      {name: "Front End",
        languages: ["HTML","CSS","Javascript"]},
      {name: "Back End",
        languages: ["Python","Java","Node.js"]},
      {name: "Database",
        languages: ["MySQL","MongoDB"]},
      {name: "Cloud",
        languages: ["AWS","Google Cloud"]},
      {name: "General Purpose",
        languages: ["C","C++"]},
      {name: "Graphic Design",
        languages: ["Illustrator","Photoshop"]},
      {name: "Frameworks",
        languages: ["Bootstrap","React Bootstrap","Jquery"]},
      {name: "Libararies",
        languages: ["Jupyter","NumPy","PyTorch", "TensorFlow"]}
    ];
  return (
    <Row>
      {stacks.map(stack => stackColumn(stack))}
    </Row>
  );
}

export default Skillsets;
